/* Editable */

#preloader{
	position: fixed;
	  width: 100%;
	  height: 100%;
	  left: 0;
	  top: 0;
	  background-color: #fff; 
	.gooey{
	  position: absolute;
	  top: 50%;
	  left: 50%;
	  width: 142px;
	  height: 40px;
	  margin: -20px 0 0 -71px;
	  background:$white;
	  filter: contrast(20);
	  .dot{
		position: absolute;
		width: 16px;
		height: 16px;
		top: 12px;
		left: 15px;
		filter: blur(4px);
		background:black;
		border-radius: 50%;
		transform: translateX(0);
		animation: dot 2.8s infinite;
	  }
	  .dots{
		transform: translateX(0);
		margin-top: 12px;
		margin-left: 31px;
		animation: dots 2.8s infinite;
			span{
			  display: block;
			  float: left;
			  width: 16px;
			  height: 16px;
			  margin-left: 16px;
			  filter: blur(4px);
			  background:black;
			  border-radius: 50%;
			}
		}
	@keyframes dot{
	  50%	{
		transform: translateX(96px);
	  }
	}
	@keyframes dots{
	  50%{
		transform: translateX(-31px);
	  }
	}
	}	
}